import { Component,OnInit } from '@angular/core';
import {Http} from '@angular/http';
import PouchDB from 'pouchdb';
declare var require: any;
PouchDB.plugin(require('pouchdb-load'));
var db = new PouchDB('pouchdb');
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {


     data;
 
  constructor(private http:Http)                                   // load json data from json file
  {
        this.http.get('./assets/data/vocabulary.json')
                .subscribe(res => this.readjson(res.json()));
  }


  title = 'app';
  public ngOnInit()                                             //  load db detail on init
  {
      db.info().then(function (info) {
      console.log(info);
    })
     console.log("Dashboard starts");

    //  db.destroy().then(function (response) {
    //   // success
    // }).catch(function (err) {
    //   console.log(err);
    // });
   
  }

readjson(data)                                                //   Read json data into db
{
    this.data = data;
    db.bulkDocs(data);
}

        
}



